import { http } from '@/api/user'
import { mapState } from 'vuex'
import CGI from '@/constant/cgi'
export default {
    name:'HomeIndex',
    async asyncData({ query, store }){
        const page = Number.parseInt(query.page || 1)
        const { tag } = query
        // 文章分页数（默认20）
        const limit = 20
        // 串行执行
        /*
        const { data } = await http({
            type:'GET', 
            url:CGI.getArticlesAPI, 
            params:{
                limit,
                // 文章偏移/跳跃数（默认0）
                offset: (page-1) * limit
                } 
            })
        // 重命名
        const { data: tagData } = await http({
                type:'GET',
                url: CGI.getTagsAPI
            })
        */
        // 优化：并发执行速度快
      const tab = query.tab || 'global_feed'

      const loadUrl = store.state.user && tab === 'your_feed'? CGI.getYourFeedAPI : CGI.getArticlesAPI
     
      const [articlesRes,tagsRes] =  await Promise.all([
            http({
                type:'GET', 
                url:loadUrl, 
                params:{
                    limit,
                    // 文章偏移/跳跃数（默认0）
                    offset: (page-1) * limit,
                    // 按照标签筛选
                    tag: tag
                    } 
            }),
            http({
                type:'GET',
                url: CGI.getTagsAPI
            })
        ])

        const { articles, articlesCount } = articlesRes.data
        articles.forEach(item =>{
            item.favoriteDisable = false
        })
        const { tags } = tagsRes.data
        return {
            articles: articles,
            articlesCount: articlesCount,
            page,
            limit,
            tags: tags,
            tag,
            tab: query.tab || 'global_feed'
        }
    },
    // page改变，组件不会重新加载，asyncData也不会被调用
    watchQuery: ['page','tag','tab'],
    computed:{
        ...mapState(['user']),
        // 总页码
        totalPage(){
            return Math.ceil(this.articlesCount / this.limit)
        }
    },
    methods:{
     // 点赞和取消点赞
      async onFavorited(article){
        // 如果网络比较慢，用户频繁点击按钮，可能会导致期间来回处理导致出现错误。因此我们应该在请求期间禁用此按钮
            article.favoriteDisable = true
            if(article.favorited){
                // 取消点赞  getDeleteFavoritedAPI
                await http({
                    type:'DELETE',
                    url:`/api/articles/${article.slug}/favorite`
                })
                article.favoritesCount += -1
                article.favorited = false
            }else{
                // 添加点赞 
                await http({
                    type:'POST',
                    url:`/api/articles/${article.slug}/favorite`
                })
                article.favoritesCount += 1
                article.favorited = true
            }
            // 请求完成后允许可点击此按钮
             article.favoriteDisable = false
        }
    }
}
